/* App.css */
.navbar {
  background-color: #34495e;
  padding: 10px;
  display: flex;
  justify-content: center;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 15px;
  padding: 0;
  margin: 0;
}

.navbar ul li {
  display: inline;
}

.navbar ul li a {
  text-decoration: none;
  color: #ecf0f1;
  font-size: 16px;
  font-weight: bold;
  transition: color 0.3s;
}

.navbar ul li a:hover {
  color: #3498db;
}
